@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$azul;
    box-shadow: .1rem .2rem .3rem #333;
    @include m.tablet{
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            z-index: 0;
            width: 100%;
        }
    }
    h1{
        color: v.$blanco;
        text-align: center;
        cursor: pointer;
        @include m.tablet{
            margin: 0;
        }
    }
    .contenido-header{
        padding: 2rem;
        @include m.tablet{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}
.navegacion-principal{
    @include m.tablet{
        display: flex;
        gap: 2rem;
    }
    a{
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        color: v.$blanco;
        font-size: 2.4rem;
        @include m.tablet{
            margin: 0;
        }
        
        &:hover{
            color: v.$amarillo;
        }
    }
}

