@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
    }
}
.galeria-imagenes{
    img{
        cursor: pointer;
    }
    @include m.resetear-lista;
    @include m.grid(2, 1fr, 2rem);
    @include m.telefono{
        @include m.grid(3, 1fr, 2rem);
    }
    @include m.tablet{
        @include m.grid(4, 1fr, 2rem);
    }
    @include m.desktop{
        @include m.grid(6, 1fr, 2rem);
    }
}
.overlay{
    position: fixed;
    padding-top: 10rem;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / .5);

    picture{
        display: block;
        max-width: 80rem;
    }
}
.btn-cerrar{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: bold;

    &:hover{
        cursor: pointer;
    }
}